.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.formContainer{
  display: flex;
  justify-content: center;
  margin-bottom: 40px ;
}
.HeaderContainer{
  background-color: blueviolet;
  color: white;
  margin: 0px 0px 40px 0px;
}
.movieList{
  display: flex;
  flex-wrap: wrap;
  gap:  40px 10px;

}

h1{
  font-size: 1.5rem;
  text-align: center;
  padding: 20px;
}
article{
  padding:20px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: auto;
  font-size: 0.8rem;
}
img{
  width: 100%;
}
p{
  font-size: 0.8rem;
  margin: 20px;
}
.movieContainer{
  padding: 0px;
  width: 350px;
  display: flex;
  flex-direction: column;
  background-color: white;
  border-radius: 8px;
  box-shadow: 2px 2px 10px rgb(162, 162, 162);

}